// БЛОК «ПЕРВЫЙ ЭКРАН»

/* Основа */

.bnr {
	position: relative;
	margin-top: rem(40);
	background: #fff;
	@include laptop {
		background-color: transparent;
	}
	&:after {
		display: none;
		position: absolute;
		z-index: -1;
		content: "";
		top: 0;
		left: 0;
		height: 100%;
		background-color: $white;
		// @include smart {
		// 	display: initial;
		// 	width: calc((100vw - 540px) / 2);
		// }
		// @include tabletm {
		// 	width: calc((100vw - 720px) / 2);
		// }
		@include laptop {
			display: initial;
			width: calc((100vw - 1140px) / 2);
		}
		@include desktop {
			width: calc((100vw - 1274px) / 2);
		}
	}
}

.banner__container {
	position: relative;
	background-color: #fff;
	padding-top: rem(40);
	padding-bottom: rem(40);

	.row {
		align-items: center;
	}
}

.blog {
	// display: flex;
	// flex-direction: column;
	// align-items: center;
	width: 100%;

	display: grid;
	justify-content: center;

	@include laptop {
		display: grid;
		grid-template-columns: 357px 1fr;

		align-items: flex-start;
		gap: rem(16) rem(56);
		padding-right: rem(56);
		grid-template-areas:
			"left tags"
			"left forms";
	}
}

/* ---------- */

/* Заголовок и автор */

.blog__title {
	margin-bottom: rem(16);
	@include smart {
		h1 {
			text-align: center;
		}
	}
}

.author {
	display: flex;
	flex-direction: column;
	border: 1px solid $border-input;
	padding: rem(16);
	margin-bottom: rem(32);
	width: 100%;

	@include smart {
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	@include laptop {
		margin-right: rem(56);
	}
}

.author__pic {
	@include bg;
	min-width: rem(120);
	min-height: rem(88);
	max-width: rem(120);
	max-height: rem(88);
	margin-bottom: rem(12);
	@include smart {
		margin-bottom: 0;
		margin-right: rem(16);
	}
	// max-width: rem(202);
	// max-height: rem(147);
}

.author__text {
	h4 {
		font-weight: 400;
		margin-bottom: rem(8);
	}
	p {
		font-size: rem(14);
		color: $text-light2;
		margin-bottom: rem(12);
	}
	button {
		border-color: #2f89ce !important;
		color: #2f89ce;
	}
}

/* ---------- */

/* Фильтр блога */

.blog__tags {
	max-width: rem(480);
	width: 100%;
	padding: rem(16);
	border: 2px solid #d1e8ee;
	margin-bottom: rem(16);

	&:hover {
		cursor: pointer;
		h3 {
			color: $btn;
		}
	}

	@include laptop {
		order: initial;
		grid-area: tags;
		max-width: 100%;
		margin-bottom: 0;
		&:hover {
			cursor: auto;
			h3 {
				color: initial;
			}
		}
	}
}

.tags-open {
	.tags__title {
		h3 {
			color: $btn;
		}
	}

	.tags__items {
		display: grid !important;
	}
}

.tags__title {
	display: flex;
	flex-direction: row;
	align-items: center;
	img {
		margin-right: rem(8);
		@include laptop {
			display: none;
		}
	}
	h3 {
		@include font(13, 22);
	}
}

.tags__items {
	display: none;

	& > * {
		padding-top: rem(16);
		padding-bottom: rem(16);
	}

	& .tags__item:last-child {
		.tags__checkboxes {
			display: none;
			@include laptop {
				display: flex;
			}
		}
	}

	@include laptop {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: rem(24);
	}
}

.tag-it-open {
	.tags__checkboxes {
		display: none;
	}
}

.tags__item {
	border-bottom: rem(1) solid $border-input;
	.item-title {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;

		p {
			@include title-font;
			font-weight: 400;
			margin-bottom: rem(8);
		}

		img {
			width: rem(12);
			transform: rotate(90deg);
			@include laptop {
				display: none;
			}
		}
	}

	@include laptop {
		border-bottom: 0;
	}
}

.tags__checkboxes {
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	div {
		display: inline-block;
		border: 1px dotted $btn;
		padding: 2px 6px;
		color: $btn;
		font-size: rem(13);
		margin-top: rem(4);
		margin-bottom: rem(4);
	}
}

/* ---------- */

/* Карточки блога */

.blog__cards {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: rem(16);

	& > * {
		margin-bottom: rem(8);
	}

	@include laptop {
		order: initial;
		grid-area: left;
	}
}

.blog__search {
	width: 100%;
	margin-bottom: rem(16);
	input {
		height: rem(40);
	}
}

.blog__card {
	display: flex;
	flex-direction: column;
	max-width: rem(480);
	padding: rem(12);
	box-shadow: 0px 3px 15px rgba(66, 121, 135, 0.17);
	@include laptop {
		max-width: rem(357);
	}
}

.pag {
	margin-top: rem(16);
}

.card-top {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin-bottom: rem(8);

	.tags,
	.data {
		display: flex;
		flex-direction: row;
		p {
			font-size: rem(11);
			color: $text-light2;
		}
	}

	.tags {
		display: none;
		@include smart {
			display: flex;
		}
		p {
			margin-right: rem(4);
		}
	}

	.data {
		@include smart {
			flex-direction: column;
			justify-content: flex-end;
			text-align: right;
		}
	}
}

.card-title {
	margin-bottom: rem(6);
	p {
		@include text-short(2);
		font-size: rem(14);
		font-weight: 700;
		@include smart {
			@include text-short(1);
		}
	}
}

.card-text {
	display: flex;
	flex-direction: row;
	align-items: center;
	p {
		display: none;
		@include smart {
			display: initial;
			font-size: rem(11);
			@include text-short(1);
			padding-right: rem(16);
		}
	}
	a {
		font-size: rem(11);
	}
}

/* Формы  */

.blog__forms {
	display: flex;
	flex-direction: column;
	max-width: rem(460);
	width: 100%;

	@include laptop {
		order: initial;
		grid-area: forms;
		flex-direction: row;
		max-width: 100%;
	}
}

.blog__it1 {
	display: flex;
	justify-content: center;
	background-color: #c7dfe7;
	margin-bottom: rem(16);
	width: 100%;
	@include laptop {
		margin-bottom: 0;
		margin-right: rem(16);
	}
}

.blog__it2 {
	width: 100%;
	display: flex;
	justify-content: center;
	background-color: #e9f2f5;
}

/* ---------- */

// КОНЕЦ БЛОКА «ПЕРВЫЙ ЭКРАН» ==========
