// СТРУКТУРА

/* База */

html {
	font-size: 16px;
}

body {
	font-family: "Roboto", Roboto, sans-serif;
	background: #f0f5f8;
}

/* ---------- */

/* Секта без отступов */

.container {
	width: 100%;
	padding-left: rem(12);
	padding-right: rem(12);
	margin-left: auto;
	margin-right: auto;
}

.row {
	display: flex;
	flex-wrap: wrap;
}

/* ---------- */

/* Breakpoints Mobile First */

@media (min-width: 576px) {
	.container {
		max-width: 540px;
	}
}

@media (min-width: 768px) {
	.container {
		max-width: 720px;
	}
}

@media (min-width: 992px) {
	.container {
		max-width: 960px;
	}
}

@media (min-width: 1200px) {
	.container {
		max-width: 1140px;
	}
}

@media (min-width: 1400px) {
	.container {
		max-width: 1274px;
	}
}

/* ---------- */

