// ИНПУТЫ

/* База ИНПУТОВ */

@mixin input($bg: $first, $borderColor: $second) {
	position: relative;
	display: block;
	width: 100%;
	padding: em(16);
	border: rem(1) solid $borderColor;
	font-family: Roboto, sans-serif;
	font-size: rem(16);
	appearance: none;
	cursor: text;
	outline: none;
	box-shadow: none;
	line-height: normal;
	background-color: $bg;
	transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;

	&::placeholder {
		color: $text;
	}

	@content;
}

/* ---------- */

/* Вид основных инпутов */

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
	@include input($input-bg, $border-input);
	position: relative;

	&:hover {
		border: rem(1) solid lighten($btn, 35%);
	}

	&:focus {
		border: rem(1) solid $btn;
	}
}

.input-box {
	position: relative;
	text-align: right;
	width: 100%;
}

/* ---------- */

/* Иконки в инпутах */

.i-b-name,
.i-b-phone,
.i-b-email {
	&:before {
		position: absolute;
		z-index: 2;
		opacity: 0.3;
		content: "";
		top: 50%;
		left: rem(18);
		transform: translateY(-50%);
		width: rem(16);
		height: rem(16);
	}
}

.i-b-name {
	&:before {
		background-image: url("../img/icons/contacts/ic-user.svg");
		background-repeat: no-repeat;
		background-size: contain;
	}
}

.i-b-phone {
	&:before {
		background-image: url("../img/icons/contacts/ic-phone.svg");
		background-repeat: no-repeat;
		background-size: contain;
	}
}

.i-b-email {
	&:before {
		width: rem(18);
		left: rem(17);
		background-image: url("../img/icons/contacts/ic-mail.svg");
		background-repeat: no-repeat;
		background-size: contain;
	}
}

