// КНОПКА

/* База кнопки */

@mixin button($bg: $first, $borderColor: $second) {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	padding: em(16) em(32) em(17) em(32);
	box-shadow: none;
	font-family: Manrope, sans-serif;
	font-size: rem(12);
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	white-space: nowrap;
	border: $borderColor;
	color: $white;
	cursor: pointer;
	background: $bg;
	@content;

	@include smart {
		font-size: rem(13);
	}
}

/* ---------- */

/* Основная кнопка */

// linear-gradient(to right, #F17519 50%, #FFAF13 100%)
.main-btn {
	@include button(
		linear-gradient(to right, #f17519 50%, #ffaf13 100%),
		transparent
	);
	background-size: 200%;
	background-position: right 0;
	transition: background 0.2s ease-in-out;

	&:hover {
		color: $white;
		background-position: 0 0;
	}

	&:active {
		background-position: right 0;
	}
}

/* ---------- */

/* Кнопка контур */

.border-btn {
	@include button(transparent, $btn);
	border-width: rem(1);
	border-style: solid;
	color: $btn;
	transition: background 0.2s ease-in-out, color 0.2s ease-in-out;

	&:hover {
		color: $white;
		background-color: $btn;
	}

	&:active {
		background-color: darken($btn, 10%);
	}
}

/* ---------- */

/* Мерцающая точка на кноке */

@keyframes blink {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

.blink {
	&:before {
		animation: blink 1.1s infinite;
	}
}

/* ---------- */

// КОНЕЦ КНОПКИ ==========
