// ТИПОГРАФИКА

/* Общие стили для заголовков, текстов и ссылок */

h1 {
	@include font(26, 32);
}

h2 {
	@include font(22, 26);
}

h3 {
	@include font(20, 22);
}

h4 {
	@include font(14, 18);
}

h5 {
	@include font(14, 20);
}

h6 {
	@include font(13, 18);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	@include title-font;
	color: $title;
	line-height: 140%;
	margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
	margin-top: 0;
}

p,
span {
	color: $text;
	line-height: 140%;
	margin-bottom: 0;
}

p,
a,
span {
	@include text-font;
}

a {
	position: relative;
	text-decoration: none;
	background-color: transparent;
	transition: color 0.2s ease-in-out;
	color: $link;

	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 0;
		height: 1px;
		opacity: 0.5;
		background-color: $link-hvr;
		transition: width 0.2s ease-in-out, background 0.2s ease-in-out;
	}

	&:hover {
		color: $link-hvr;

		&::before {
			width: 100%;
			background-color: $link-hvr;
		}
	}
}

*:focus:not(.focus-visible) {
	outline: none;
}

/* ---------- */

