// МИКСИНЫ

/* Основные семейства шрифтов для заголовков и текста */

@mixin title-font {
	font-family: "Roboto Slab", sans-serif;
	font-weight: 700;
}

@mixin text-font {
	font-family: Roboto, sans-serif;
	font-weight: 400;
}

/* ---------- */

/* Адаптивный шрифт */

$screen-min: 280;
$screen-max: 1600;

@mixin font($font-min, $font-max) {
	font-size: #{$font-min}px;

	@media only screen and (min-width: #{$screen-min}px) {
		font-size: calc(
			#{$font-min}px + #{($font-max - $font-min)} * (100vw - #{$screen-min}px) /
				(#{$screen-max} - #{$screen-min})
		);
	}

	@media only screen and (min-width: #{$screen-max}px) {
		font-size: #{$font-max}px;
	}
}

/* ---------- */

/* Перевод rem|em в px */

$html-font-size: 16;

@function stripUnit($value) {
	@return calc($value / ($value * 0 + 1));
}
@function rem($pxValue) {
	@return #{calc(stripUnit($pxValue) / stripUnit($html-font-size))}rem;
}

@function em($pixels, $context: $html-font-size) {
	@return #{calc($pixels / $context)}em;
}

/* ---------- */

/* Для быстрой проверки расположения блоков*/

@mixin br {
	border: 1px solid lightgreen;
}

@mixin br-r {
	border: 1px solid lightcoral;
}

@mixin br-bl {
	border: 1px solid lightskyblue;
}

@mixin bg {
	background-color: rgba(144, 238, 144, 0.2);
}

/* ---------- */

/* Брекпоинты Bootstrap */

@mixin smart {
	@media (min-width: 576px) {
		@content;
	}
}

@mixin tabletm {
	@media (min-width: 768px) {
		@content;
	}
}

@mixin tabletb {
	@media (min-width: 992px) {
		@content;
	}
}

@mixin laptop {
	@media (min-width: 1200px) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: 1400px) {
		@content;
	}
}

/* ---------- */

/* для использования колонок */

@mixin row {
	display: flex;
	flex-wrap: wrap;
}

/* ---------- */

/* Колонки как в Bootstrap */

@mixin col {
	flex: 1 0 0%;
}

@mixin col-1 {
	flex: 0 0 auto;
	width: 8.33333333%;
}

@mixin col-2 {
	flex: 0 0 auto;
	width: 16.66666667%;
}

@mixin col-3 {
	flex: 0 0 auto;
	width: 25%;
}

@mixin col-4 {
	flex: 0 0 auto;
	width: 33.33333333%;
}

@mixin col-5 {
	flex: 0 0 auto;
	width: 41.66666667%;
}

@mixin col-6 {
	flex: 0 0 auto;
	width: 50%;
}

@mixin col-7 {
	flex: 0 0 auto;
	width: 58.33333333%;
}

@mixin col-8 {
	flex: 0 0 auto;
	width: 66.66666667%;
}

@mixin col-9 {
	flex: 0 0 auto;
	width: 75%;
}

@mixin col-10 {
	flex: 0 0 auto;
	width: 83.33333333%;
}

@mixin col-11 {
	flex: 0 0 auto;
	width: 91.66666667%;
}

@mixin col-12 {
	flex: 0 0 auto;
	width: 100%;
}

/* ---------- */

/* Offset как в Bootstrap */

@mixin offset-0 {
	margin-left: 0;
}

@mixin offset-1 {
	margin-left: 8.33333333%;
}

@mixin offset-2 {
	margin-left: 16.66666667%;
}

@mixin offset-3 {
	margin-left: 25%;
}

@mixin offset-4 {
	margin-left: 33.33333333%;
}

@mixin offset-5 {
	margin-left: 41.66666667%;
}

@mixin offset-6 {
	margin-left: 50%;
}

@mixin offset-7 {
	margin-left: 58.33333333%;
}

@mixin offset-8 {
	margin-left: 66.66666667%;
}

@mixin offset-9 {
	margin-left: 75%;
}

@mixin offset-10 {
	margin-left: 83.33333333%;
}

@mixin offset-11 {
	margin-left: 91.66666667%;
}

/* ---------- */

/* Gutters как в Bootstrap */

@mixin g-0 {
	--bs-gutter-x: 0;
	--bs-gutter-y: 0;
}

@mixin gx-0 {
	--bs-gutter-x: 0;
}

@mixin gy-0 {
	--bs-gutter-y: 0;
}

@mixin g-1 {
	--bs-gutter-x: 0.25rem;
	--bs-gutter-y: 0.25rem;
}

@mixin gx-1 {
	--bs-gutter-x: 0.25rem;
}

@mixin gy-1 {
	--bs-gutter-y: 0.25rem;
}

@mixin g-2 {
	--bs-gutter-x: 0.5rem;
	--bs-gutter-y: 0.5rem;
}

@mixin gx-2 {
	--bs-gutter-x: 0.5rem;
}

@mixin gy-2 {
	--bs-gutter-y: 0.5rem;
}

@mixin g-3 {
	--bs-gutter-x: 1rem;
	--bs-gutter-y: 1rem;
}

@mixin gx-3 {
	--bs-gutter-x: 1rem;
}

@mixin gy-3 {
	--bs-gutter-y: 1rem;
}

@mixin g-4 {
	--bs-gutter-x: 1.5rem;
	--bs-gutter-y: 1.5rem;
}

@mixin gx-4 {
	--bs-gutter-x: 1.5rem;
}

@mixin gy-4 {
	--bs-gutter-y: 1.5rem;
}

@mixin g-5 {
	--bs-gutter-x: 3rem;
	--bs-gutter-y: 3rem;
}

@mixin gx-5 {
	--bs-gutter-x: 3rem;
}

@mixin gy-5 {
	--bs-gutter-y: 3rem;
}

/* ---------- */

/* Троеточие в конце строки */

@mixin text-short($numLines: 1) {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;

	@supports (-webkit-line-clamp: $numLines) {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: initial;
		display: -webkit-box;
		-webkit-line-clamp: $numLines;
		-webkit-box-orient: vertical;
	}
}

/* ---------- */

/* Фоновые картинки для ретины */

@mixin bg-x2($link) {
	@media only screen and (-webkit-min-device-pixel-ratio: 2),
		only screen and (min--moz-device-pixel-ratio: 2),
		only screen and (-o-min-device-pixel-ratio: 2/1),
		only screen and (min-device-pixel-ratio: 2),
		only screen and (min-resolution: 192dpi),
		only screen and (min-resolution: 2dppx) {
		background-image: url($link);
	}
}

/* ---------- */

/* Анимации появления текста */

@mixin text-anim() {
	transform: translate(0px, 20%);
	opacity: 0;
	transition: all 0.8s ease 0.3s;
}

@mixin text-anim-show() {
	transform: translate(0px, 0);
	opacity: 1;
}

/* ---------- */

/* Вертикальное выравнивание */

@mixin v-center {
	top: 50%;
	-ms-transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}

/* ---------- */

/* Скрыть но оставить */

@mixin visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

/* ---------- */

/* Тень */

@mixin shd-l {
	box-shadow: 0px 0.25rem 0.375rem rgba(0, 0, 0, 0.06);
}

@mixin shd-m {
	box-shadow: 0px 0.9375rem 1.25rem rgba(0, 0, 0, 0.05);
}

@mixin shd-h {
	box-shadow: 0px 1.25rem 3.125rem rgba(0, 0, 0, 0.1);
}

